.NoteEditor {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    max-width: 400px;
    width: 100%;
    margin: 0 auto;
    padding: 8px;
    border-radius: 2px;
    -webkit-box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 1px 5px 0px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12);
  }
  
  .NoteEditor-input {
    border-color: #616161;
    margin-bottom: 16px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 8px;
    font-size: 16px;
    font-family: inherit;
    resize: none;
    border-radius: 2px;
  }

  .Title {
    text-align: center;
  }


