.Button {
    position: relative;
    border: none;
    border-radius: 3px;
    margin: 0 4px;
    padding: 0 16px;
    vertical-align: top;
    outline: none;
  
    font-size: 14px;
    line-height: 2.8;
    color: #222;
    text-transform: uppercase;
    text-align: center;
    white-space: nowrap;
  
    background-color: #fff;
    overflow: hidden;
    z-index: 0;
    cursor: pointer;
  
    -webkit-box-shadow: 0 2px 1px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3);
  
            box-shadow: 0 2px 1px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3);
    -webkit-transition: background-color 0.3s, opacity 0.3s, color 0.3s, -webkit-box-shadow 0.3s;
    transition: background-color 0.3s, opacity 0.3s, color 0.3s, -webkit-box-shadow 0.3s;
    -o-transition: background-color 0.3s, box-shadow 0.3s, opacity 0.3s, color 0.3s;
    transition: background-color 0.3s, box-shadow 0.3s, opacity 0.3s, color 0.3s;
    transition: background-color 0.3s, box-shadow 0.3s, opacity 0.3s, color 0.3s, -webkit-box-shadow 0.3s;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  
    min-width: 32px;
    min-height: 36px;
    font-weight: 500;
    font-family: inherit;
  
    -webkit-transition: background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      -webkit-box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  
    transition: background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      -webkit-box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  
    -o-transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  
    transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1);
  
    transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      background-color 0.28s cubic-bezier(0.4, 0, 0.2, 1),
      -webkit-box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
    cursor: pointer;
  }
  
  .Button:active {
    -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.3);
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.3);
  }

  .Button:hover {
    -webkit-box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.3);
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.3);
    background-color: aliceblue;
  }


